.product-buy-wrapper {
  display: block;
  text-align: center;
  margin-bottom: 36px;
  z-index: 10;
}

@media (min-width: @tablet-width) {
  .product-buy-wrapper {
    grid-column: 1/2;
    grid-row: 2/3;
    margin-top: auto;
    margin-bottom: 0;
  }
}

@media (min-width: @desktop-width) {
  .product-buy-wrapper {
    grid-column: 2/4;
    grid-row: 2/3;
    margin-right: 58px;
  }
}
